.title__text{
  display: none;
}

.title__cursor{
  color: #0E9EDD;
  -webkit-animation: .7s blink infinite;
  animation: .7s blink infinite;
  font-weight: 700;
}

@-webkit-keyframes blink {
  50% {
    opacity: 0.2;
  }

}

@keyframes blink {
  50% {
    opacity: 0.2;
  }
}
